  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

main {
  background-color: rgba(1, 0, 74, 0.8);
}

header {
  background-color: rgba(1, 21, 74, 0.8);
  padding: 3rem;
}

.logo-style {
  color: rgba(76, 6, 150, 0.88);
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

nav h3 {
  font-size: 50px;
  font-family: "Permanent Marker", cursive;
}

ul {
  display: flex;
  gap: 70px;
}

ul li {
  list-style-type: none;
  text-decoration: none;
  font-size: 25px;
  color: #fff;
  font-family: "Gothic A1", sans-serif;
}

ul li a {
  text-decoration: none;
  font-size: 25px;
  color: #fff;
  font-family: "Gothic A1", sans-serif;
}

h2 {
  color: #fff;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 2rem;
  text-align: center;
}

.movie-list {
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  margin: 1rem;
  padding: 1rem;
}

.buttons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 1rem;
  padding: 1rem;
  gap: 1rem;
}

.reservation-button,
.comment-button {
  font-family: "Courier New", Courier, monospace;
  font-weight: 400;
  color: #5799ef;
  font-size: 1rem;
}

footer {
  display: block;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  padding: 8px;
  height: 30px;
  color: #fff;
  border-color: black;
  background-color: rgba(1, 21, 74, 0.8);
  border: 1cm;
}

.overlay {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  z-index: 1000;
  color: black;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.popup {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  z-index: 1001;
  color: black;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  overflow-y: auto;
  max-height: 80%;
  padding-top: 300px;
}

.popup h2 {
  color: black;
}

.reservation-form {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.reservation-form input[type=text],
.reservation-form input[type=date] {
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

.reservation-form button {
  background-color: #e53935;
  color: #fff;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 20px;
}

textarea {
  background: #fff;
  height: 100px;
  border: 1px solid #cfd8dc;
  border-radius: 8px;
  margin-top: 18px;
  padding: 15px 38px 15px 16px;
  font-family: Poppins, serif, sans serif;
  font-weight: 400;
  color: #172b4d;
  font-size: 17px;
}

.comment-form input[type=text],
.comment-form textarea,
.comment-form button {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.comment-form button {
  background: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

.buttons button {
  padding: 0.5rem;
  border: none;
  border-radius: 0.5rem;
  background-color: #393434;
  cursor: pointer;
}

.buttons button:hover {
  background-color: #f5c518;
}

.buttons button:active {
  background-color: #0a0a0a;
}

.comment-form button:hover {
  background: #0056b3;
}

.reservations-container {
  border: 1px solid #000;
  padding: 10px;
  margin-top: 20px;
  border-radius: 4px;
}

.reservation-details {
  margin-bottom: 10px;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #e53935;
  color: #fff;
  border: none;
  padding: 5px 10px;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
  z-index: 2;
}

.totalReservations {
  font-weight: bold;
  border: 1px solid #000;
  padding: 5px;
  display: inline-block;
}

.like-button {
  width: 20px;
  height: 20px;
}

.like-counter {
  font-weight: 100;
  color: red;
}

.item-count {
  color: #fff;
  font-weight: 100;
}

/* Default styles for the buttons */
.us-button,
.canada-button,
.india-button {
  cursor: pointer;
  color: white;
}

.us-button:hover,
.canada-button:hover,
.india-button:hover {
  color: #fff;
}

#myModal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

/* Modal Content */
.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 70%;
}

/* The Close Button */
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.modal-content h1 {
  text-align: center;
  margin-bottom: 20px;
  font-family: "Gothic A1", sans-serif;
}

.details {
  display: grid;
  grid-template-columns: 1fr 1fr;
  text-align: center;
  font-family: "Gothic A1", sans-serif;
  font-size: 20px;
  padding-bottom: 20px;
}

.details p {
  padding-bottom: 15px;
}

#movieImg {
  width: 20%;
  display: block;
  margin: 0 auto;
  padding-bottom: 20px;
}

.comments {
  text-align: center;
  font-family: "Gothic A1", sans-serif;
  font-size: 20px;
}

.comments p {
  padding-bottom: 15px;
}

.form-center {
  width: 400px;
  margin: 0 auto;
}

input {
  border-radius: 0;
  border-radius: 8px;
  padding: 15px 38px 15px 16px;
  border: 1px solid #cfd8dc;
  margin-top: 20px;
  font-family: Poppins, serif, sans serif;
  font-weight: 400;
  color: #172b4d;
  font-size: 17px;
}

.comment-btn {
  font-family: Poppins, serif, sans serif;
  background-color: #fff;
  border: 1px solid #cfd8dc;
  border-radius: 8px;
  color: #8f9394;
  padding: 12px;
  text-align: left !important;
  text-decoration: none;
  cursor: pointer;
  font-weight: 500;
  font-size: 17px;
  line-height: 24px;
  margin-top: 15px;
  letter-spacing: 0.03em;
  transition: background-color 0.15s, color 0.15s;
}

.comments-container {
  border: 1px solid #ccc; /* Gray border */
  padding: 10px;
  margin-top: 20px;
  max-height: 300px; /* Add max-height for scrolling */
  overflow-y: auto; /* Add scrollbars when content overflows */
}

/* Style individual comments */
.comments-container p {
  margin: 0;
  padding: 5px 0;
  border-bottom: 1px solid #ccc; /* Add horizontal line between comments */
}

/* Style the total comments text */
.total-comments {
  margin-top: 10px;
  font-weight: bold;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAEA;EACE,SAAA;EACA,UAAA;EACA,sBAAA;AAAF;;AAGA;EACE,qCAAA;AAAF;;AAGA;EACE,sCAAA;EACA,aAAA;AAAF;;AAGA;EACE,6BAAA;AAAF;;AAGA;EACE,aAAA;EACA,6BAAA;EACA,mBAAA;AAAF;;AAGA;EACE,eAAA;EACA,wCAAA;AAAF;;AAGA;EACE,aAAA;EACA,SAAA;AAAF;;AAGA;EACE,qBAAA;EACA,qBAAA;EACA,eAAA;EACA,WAAA;EACA,oCAAA;AAAF;;AAGA;EACE,qBAAA;EACA,eAAA;EACA,WAAA;EACA,oCAAA;AAAF;;AAGA;EACE,WAAA;EACA,iCAAA;EACA,gBAAA;EACA,eAAA;EACA,kBAAA;AAAF;;AAGA;EACE,aAAA;EACA,qBAAA;EACA,qCAAA;EACA,cAAA;EACA,YAAA;EACA,aAAA;AAAF;;AAGA;EACE,aAAA;EACA,sBAAA;EACA,uBAAA;EACA,YAAA;EACA,aAAA;EACA,SAAA;AAAF;;AAGA;;EAEE,8CAAA;EACA,gBAAA;EACA,cAAA;EACA,eAAA;AAAF;;AAGA;EACE,cAAA;EACA,uBAAA;EACA,mBAAA;EACA,kBAAA;EACA,WAAA;EACA,YAAA;EACA,YAAA;EACA,WAAA;EACA,mBAAA;EACA,sCAAA;EACA,WAAA;AAAF;;AAGA;EACE,aAAA;EACA,eAAA;EACA,MAAA;EACA,OAAA;EACA,WAAA;EACA,YAAA;EACA,8BAAA;EACA,aAAA;EACA,YAAA;EACA,uBAAA;EACA,mBAAA;EACA,sBAAA;AAAF;;AAGA;EACE,kBAAA;EACA,QAAA;EACA,SAAA;EACA,gCAAA;EACA,sBAAA;EACA,aAAA;EACA,uCAAA;EACA,aAAA;EACA,YAAA;EACA,mBAAA;EACA,uBAAA;EACA,kBAAA;EACA,sBAAA;EACA,gBAAA;EACA,eAAA;EACA,kBAAA;AAAF;;AAGA;EACE,YAAA;AAAF;;AAGA;EACE,gBAAA;EACA,aAAA;EACA,sBAAA;AAAF;;AAGA;;EAEE,aAAA;EACA,mBAAA;EACA,sBAAA;EACA,kBAAA;EACA,eAAA;AAAF;;AAGA;EACE,yBAAA;EACA,WAAA;EACA,YAAA;EACA,kBAAA;EACA,eAAA;EACA,kBAAA;EACA,eAAA;EACA,gBAAA;AAAF;;AAGA;EACE,gBAAA;EACA,aAAA;EACA,yBAAA;EACA,kBAAA;EACA,gBAAA;EACA,4BAAA;EACA,uCAAA;EACA,gBAAA;EACA,cAAA;EACA,eAAA;AAAF;;AAGA;;;EAGE,WAAA;EACA,aAAA;EACA,gBAAA;EACA,sBAAA;EACA,kBAAA;AAAF;;AAGA;EACE,mBAAA;EACA,YAAA;EACA,YAAA;EACA,eAAA;AAAF;;AAGA;EACE,eAAA;EACA,YAAA;EACA,qBAAA;EACA,yBAAA;EACA,eAAA;AAAF;;AAGA;EACE,yBAAA;AAAF;;AAGA;EACE,yBAAA;AAAF;;AAGA;EACE,mBAAA;AAAF;;AAGA;EACE,sBAAA;EACA,aAAA;EACA,gBAAA;EACA,kBAAA;AAAF;;AAGA;EACE,mBAAA;AAAF;;AAGA;EACE,kBAAA;EACA,SAAA;EACA,WAAA;EACA,yBAAA;EACA,WAAA;EACA,YAAA;EACA,iBAAA;EACA,eAAA;EACA,kBAAA;EACA,eAAA;EACA,UAAA;AAAF;;AAGA;EACE,iBAAA;EACA,sBAAA;EACA,YAAA;EACA,qBAAA;AAAF;;AAGA;EACE,WAAA;EACA,